<template>
    <div>
        <v-checkbox
            :label="txt"

            v-model="def_check"
            @click="done"

            :class="extra_class"
        ></v-checkbox>
    </div>
</template>

<script>
    export default {
        props: {
            txt: {
                type: String
            },
            v: {
                type: Object,
                default: null
            },
            def_check: {
                type: Boolean,
                default: false
            },
            extra_class: {
                type: String
            }
        },
        name: '',
        data() {
            return {
                check: false
            }
        },
        mounted() {
            this.check = this.def_check
        },
        methods: {
            done() {
                this.$emit('done_Father', this.check, this.v)
            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


